*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

div{
  color: black;
}

.order-form{
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 700px;
}

.order-heading{
  margin-bottom: 15px;
}

.order-header{
  display: flex;
  width: 100%;
}

.order-header > .price{
  margin-right: 35px;
}

.order-header > .ks{
  width: 30px;
}

.product{
  margin-right: auto;
}

.product-cont{
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.product-cont > .product-img{
  object-fit: contain;
  display: block;
  max-width: 40px;
  margin-right: 20px;
}

.product-cont > .price{
  margin-left: auto;
  margin-right: 30px;
}

.product-cont > .add-to-cart{
  height: 25px;
  width: 25px;
  margin-left: 10px;
  text-align: center;
}

ion-icon{
  cursor: pointer;
}

ion-icon{
  font-size: 30px;
}

.product-cont > .set-quant{
  width: 25px;
}

.product-cont > .arrows{
width: 10px;
}

.summary-heading{
  margin-top: 20px;
  margin-bottom: 20px;
}

.price-summary{
  margin-top: 20px;
}
.price-summary > .price-block{
display: flex;
}
.price-summary > .price-block > .price{
  margin-left: auto;
}

.filters{
  margin: 10px 0;
  display: flex;
}

.filters > div {
  padding: 10px;
  cursor: pointer;
  border: 1px solid black;
  margin-right: 10px;
  transition: .3s all;
}

.filters > div:hover{
  background-color: black;
  color: white;
}

.contactForm > h4{
  margin-top: 20px;
}

.contactInfo{
  display: flex;
  justify-content: space-between;
}
.contactInput{
  width: 20%;
}
